@import url(https://fonts.googleapis.com/css2?family=Mulish&family=Nunito&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  background-color: #131313;
  color: #fff;
  font-family: "Mulish", sans-serif;
}

.App {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  padding: 3rem;
  min-height: 100vh;
}

@media screen and (max-width: 702px) {
  .App {
    padding: 1rem;
  }
}

.operations {
  display: flex;
  justify-content: center;
  padding: 1rem 2rem;
  margin-bottom: 2rem;
  position: fixed;
  top: 0;
  width: 100%;
  z-index: 100;
}

.country select {
  padding: 0.5rem 1rem;
  background: linear-gradient(90deg, #f8ff00 0%, #3ad59f 100%);
  color: #212121;
  text-transform: uppercase;
  font-weight: 600;
  border-radius: 1rem;
  outline: none;
  margin-left: 2rem;
}

.country select option {
  text-transform: uppercase;
  background-color: #212121;
}

#search {
  padding: 0.5rem 1rem;
  width: 15rem;
  border-radius: 1rem;
  border: 1.2px solid #f8ff00;
  outline: none;
  background-color: #121212;
  color: #fff;
}

@media screen and (max-width: 702px) {
  #search {
    width: 10.2rem;
  }
}

.coinContainer {
  width: 70%;
  margin: auto;
  padding-top: 1.5rem;
  margin-top: 3rem;
}

.coinContainer::-webkit-scrollbar {
  display: none;
}

@media screen and (max-width: 702px) {
  .coinContainer {
    width: 100%;
  }
}

.coinImage {
  width: 30px;
  height: 30px;
}

.symbol {
  text-transform: uppercase;
}

table,
td,
th {
  text-align: left;
  font-size: 1rem;
}

table {
  border-collapse: collapse;
  width: 100%;
}

table th {
  position: -webkit-sticky;
  position: sticky;
  top: 0;
}

th,
td {
  padding: 15px;
  border-bottom: 0.2px solid #dbdbdb;
}

.coinPercent_red {
  color: #ff1d1d;
}

.coinPercent_green {
  color: #3eff3e;
}

.mobileContainer {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  margin-top: 3rem;
}
/*# sourceMappingURL=style.css.map */
.coinCard {
  display: flex;
  justify-content: space-between;
  width: 100%;
  padding: 1rem;
  margin-bottom: 1rem;
  border-bottom: 0.2px solid #dbdbdb;
}

.imageIcon {
  width: 10%;
}

.imageIcon img {
  width: 1.5rem;
  height: 1.5rem;
}

.title {
  width: 40%;
}

.priceData {
  width: 50%;
  text-align: right;
}

.title, .priceData {
  margin-left: 1rem;
}

.title h1, .priceData h1 {
  font-size: 1rem;
}

.title p, .priceData p {
  font-size: 0.8rem;
  text-transform: uppercase;
}
/*# sourceMappingURL=coins.css.map */
